/* Original Styles For Numbers, before icons added in */
.Die {
  /* font-size: 50px; */
  /* display: flex;
  justify-content: center;
  align-items: center; */
  padding: 0;
  font-family: monospace;
  font-weight: bold;
  background-color: transparent;
  color: white;
  border: none;
  /* width: 40px;
  height: 40px; */
}

/* THE NICER STYLES MADE FOR THE FONT-AWESOME ICONS... */

.Die{
  /* color: white; */
  /* text-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1); */
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  -webkit-filter: drop-shadow(0 15px 12px rgba(0, 0, 0, 0.5));
  filter: drop-shadow(0 15px 12px rgba(0, 0, 0, 0.5));
}

.Die-locked {
  opacity: 0.5;
  /* text-shadow: none; */
  -webkit-filter: none;
          filter: none;
}

.Die:not(.Die-locked):hover {
  cursor: pointer;
  opacity: 0.8;
}
.Die-rolling {
  -webkit-animation: spin 1s ease-out;
          animation: spin 1s ease-out;
}

.Die-rolling:hover,
.Die[disabled] {
  cursor: not-allowed;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
} 



