.RuleRow {
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  border: none;
  border-bottom: 1px solid #333;
  padding: 10px;      
  border-radius:0;
}
.RuleRow:first-child{
  border-top: 1px solid #333;
}
.RuleRow-active:hover {
  cursor: pointer;
  background: rgba(227, 242, 253, 0.5);
}

.RuleRow-disabled {
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgb(233, 232, 196)),
    color-stop(0%, rgb(233, 232, 196)),
    color-stop(30%, rgb(233, 232, 196)),
    color-stop(40%, rgb(233, 253, 227))
  );
  background: -webkit-linear-gradient(
    left,
    rgb(233, 232, 196) 0%,
    rgb(233, 232, 196) 0%,
    rgb(233, 232, 196) 30%,
    rgb(233, 253, 227) 40%
  );
  background: -o-linear-gradient(
    left,
    rgb(233, 232, 196) 0%,
    rgb(233, 232, 196) 0%,
    rgb(233, 232, 196) 30%,
    rgb(233, 253, 227) 40%
  );
  background: linear-gradient(
    to right,
    rgb(233, 232, 196) 0%,
    rgb(233, 232, 196) 0%,
    rgb(233, 232, 196) 30%,
    rgb(233, 253, 227) 40%
  );
  background-size: 300% 300%;
  -webkit-animation: Slide 1s ease 1;
          animation: Slide 1s ease 1;
}

@-webkit-keyframes Slide {
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 51%;
  }
}

@keyframes Slide {
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 51%;
  }
}

.RuleRow-name {
  display: inline-block;
  position: relative;
  /* text-decoration: line-through; */
}
.RuleRow-name::before {
  -webkit-transition: -webkit-transform 0.5s ease;
  transition: -webkit-transform 0.5s ease;
  -o-transition: transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  content:'';
  position: absolute;
  height:100%;
  width: calc(100% + 10px);
  border-top: 1px solid black;
  top:50%;
  left:-5px;
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: left;
      -ms-transform-origin: left;
          transform-origin: left;
}
.RuleRow-disabled .RuleRow-name::before {
  -webkit-transform: scaleX(100%);
      -ms-transform: scaleX(100%);
          transform: scaleX(100%);
  /* background-color: green; */
}

.RuleRow-disabled:hover {
  cursor: not-allowed;
}

